.App {
  font-family: Arial, sans-serif;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  min-height: 100vh;
}

.tab-container {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  background-color: #f8f9fa;
  padding: 10px;
  border-radius: 8px;
  gap: 10px;
}

.tab-button {
  padding: 10px 20px;
  border: none;
  background: none;
  cursor: pointer;
  font-size: 16px;
  color: #666;
  transition: all 0.3s ease;
  text-decoration: none;
}

.tab-button:hover {
  color: #333;
}

.tab-button.active {
  color: #2c3e50;
  font-weight: bold;
  border-bottom: 2px solid #2c3e50;
}

.auth-buttons {
  display: flex;
  gap: 10px;
  margin-left: auto;
}

.auth-link {
  padding: 8px 16px;
  background-color: #2c3e50;
  color: white;
  border-radius: 4px;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

.auth-link:hover {
  background-color: #34495e;
}

/* Responsive Design */
@media (max-width: 768px) {
  .tab-container {
    flex-wrap: wrap;
  }
  
  .tab-button {
    padding: 8px 15px;
    font-size: 14px;
  }

  .auth-buttons {
    width: 100%;
    justify-content: center;
    margin-top: 10px;
  }
} 